/**
 * axios
 */

import axios from 'axios'
import router from '../../router'
import qs from 'query-string';
import ElementUI from 'element-ui';
import Vue from 'vue'
Vue.use(ElementUI);
import crypto from "crypto";
import * as types from '@/store/types'
window.imgUr='http://support.bk.cloudns.cc:8080/admin/v1/file?id=';
var config="http://support.bk.cloudns.cc:8080";
console.log(process)
if(window.origin.indexOf('localhost')>-1){
	var config="/api";
}
/**
 * axios
 * @param {object} config axios
 * @return {object} promise error
 **/
let loading;
let i = 0;
axios.interceptors.request.use(config => {
	if(config.url.indexOf('/payment/paypal/info/' == -1)) {
		i++;

		loading = Vue.prototype.$loading({
			lock: true,
			text: '',
			spinner: 'el-icon-loading',
			background: 'rgba(0, 0, 0,0)'
		});

	}

	return config;
}, error => {
	return Promise.reject(error);
})

/**
 * axios 
 * @param {object} response error
 * @return {object} promise error
 **/

axios.interceptors.response.use(response => {
	i--
	if(i <=0) {
		loading.close();
	}
	if(response.data && typeof response.data == "object") {
		if(response.data.retCode === -1) {
			errorMessaage(response.data.msg || response.data.message, true);
			return Promise.reject(response);
		}
	}
	return response.data;
}, error => { //
	if(error.response) {
		if(error.response.status === 504 || error.response.status === 404) {
			errorMessaage("");
		} else if(error.response.status === 403) {
			errorMessaage("");
		} else {
			errorMessaage("");
		}
	}
	return Promise.reject(error);
})

//
const errorOptionsPushPage = {
	type: "error",
	confirmButtonText: "",
	callback: backLogin,
	showClose: false,
	center: true
}
const errorOptionsNoPushPage = {
	type: "error",
	confirmButtonText: "",
	showClose: true,
	center: true
}
//
function backLogin() {
	router.replace({
		name: 'login'
	});
}
//
function errorMessaage(msg, noPushPageFlag) {
	if(noPushPageFlag) {
		ElementUI.MessageBox.alert(msg, errorOptionsNoPushPage);
	} else {
		ElementUI.MessageBox.alert(msg, errorOptionsPushPage);
	}
}

export default {
	axiosGet(url, params) {
		return new Promise(function(resolve, reject) {
			axios.get(config + url, params).then((response) => {
				resolve(response);
			})
		})
	},
	axiosPost(url, params) {
		return new Promise(function(resolve, reject) {

			axios.post(config + url, params).then((response) => {
				resolve(response);
			})
		})
	},
	axiosDelete(url, params) {
		return new Promise(function(resolve, reject) {

			axios.delete(config + url, {
				data: params
			}).then((response) => {
				resolve(response);
			})
		})
	}
}


